/**
 *
 * @title Tab
 * @description tabBarPositionTab,`left``right`Tabs
 *
 */

import React, { Component } from 'react';
import Tabs from '../../src';

const {TabPane} = Tabs;

class Demo2 extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            activeKey: "1",
            start: 0,
        })
    }
    onChange = (activeKey) => {
        console.log(`onChange ${activeKey}o-^-o`);
        this.setState({
            activeKey,
        });
    }

    onTabClick = (key) => {
        console.log(`onTabClick ${key}o^o`);
        if (key === this.state.activeKey) {
            this.setState({
                activeKey: '',
            });
        }
    }

    render() {

        return (
            <div style={{ margin: 20 }}>
                <Tabs
                    activeKey={this.state.activeKey}
                    tabBarPosition="left"
                    defaultActiveKey="1"
                    onChange={this.onChange}
                    className="demo2-tabs"
                >
                    <TabPane tab="" key="1" >
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                    </TabPane>
                    <TabPane tab="" key="2">
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                    </TabPane>
                    <TabPane tab="" key="3">
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                    </TabPane>
                    <TabPane tab="" key="4">
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                    </TabPane>
                </Tabs>
            </div>
        )
    }
}

export default Demo2;


