// export default from './Tabs';
// 20181211animatedscrollAnimatedScrollableTabBarMixininkBarAnimatedInkTabBarMixintrue
// 
import createClass from 'create-react-class';
import React from "react";
import { Tabs as TabsInner } from './Tabs';

const Tabs = createClass({
    render() {
        const disableProps = {
            animated: false,
            scrollAnimated: false, // scrollableBarclass
            inkBarAnimated: false,// inkBarclass
            useTransform3d: false,//translate3d
        };
        return (<TabsInner  {...this.props} {...disableProps}/>)
    }
});
Tabs.TabPane = TabsInner.TabPane;
export default Tabs;




