/**
 *  @title Tab
 *  @description TabsTabPaneTab
 *
 */

import React, {Component} from 'react';
import Tabs from '../../src';

const {TabPane} = Tabs;

function callback() {

}

class Demo1 extends Component {
    render() {
        return (
            <Tabs
                defaultActiveKey="1"
                onChange={callback}
                tabBarStyle="upborder"
                className="demo1-tabs"
                style={{width:'500px'}}
            >
                <TabPane tab='' key="1">
                    <ul>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>


                </TabPane>
                <TabPane tab='2' key="1-2">
                    <ul>
                        <li></li>
                        <li>2</li>
                        <li></li>
                        <li>2</li>
                    </ul>


                </TabPane>
                <TabPane tab='' key="2">
                    <ul>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </TabPane>
                <TabPane tab='' key="3">
                    <ul>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </TabPane>
                <TabPane tab='' key="4">
                    <ul>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </TabPane>
            </Tabs>
        )
    }
}

export default Demo1;


