/**
 *
 * @title Tab
 * @description TabPaneScrollableInkTabBar
 *
 */

import React, {Component} from 'react';
import Tabs from '../../src';
import Icon from 'bee-icon';

const {TabPane} = Tabs;

let index = 1;
const content = `

`;

class Demo3 extends Component {
    state = {
        tabs: [{
            title: '',
            content: content
        }],
        activeKey: '',
    };

    onTabChange = (activeKey) => {
        this.setState({
            activeKey,
        });
    }

    construct() {
        const disabled = true;
        return this.state.tabs.map((t) => {
            return (
                <TabPane
                tab={<span>{t.title}
                    <Icon
                        type="uf-close-c"
                        className="close-icon"
                        onClick={this.remove.bind(this, t.title)}
                    />
                </span>}
                key={t.title}
            >
                <div style={{padding: 100}}>
                    {t.content}
                </div>
            </TabPane>);
        }).concat([
            <TabPane
                tab={<a style={{color: 'black', cursor: 'pointer'}} onClick={this.add}> + </a>}
                disabled={disabled}
                key={'__add'}
            />,
        ]);
    }

    // 
    remove = (title, e) => {
        e.stopPropagation();
        if (this.state.tabs.length === 1) {
            alert('');
            return;
        }
        let foundIndex = 0;
        const after = this.state.tabs.filter((t, i) => {
            if (t.title !== title) {
                return true;
            }
            foundIndex = i;
            return false;
        });
        let activeKey = this.state.activeKey;
        if (activeKey === title) {
            if (foundIndex) {
                foundIndex--;
            }
            activeKey = after[foundIndex].title;
        }
        this.setState({
            tabs: after,
            activeKey,
        });
    }

    clear = () => {
        let { tabs } = this.state;
        this.setState({
            tabs:  [tabs.shift()],
            activeKey: '',
        })
    }

    // 
    add = (e) => {
        e.stopPropagation();
        index++;
        const newTab = {
            title: `: ${index}`,
            content: `: ${content}`,
        };
        this.setState({
            tabs: this.state.tabs.concat(newTab),
            activeKey: `: ${index}`,
        });
    }

    render() {

        return (
            <div style={{margin: 20}}>

                <Tabs
                    activeKey={this.state.activeKey}
                    onChange={this.onTabChange}
                    tabBarStyle="primary"
                    defaultActiveKey=""
                    extraContent={
                        <Icon
                            type="uf-del"
                            className="clear-icon"
                            onClick={this.clear}
                            title=""
                        />
                    }
                >
                    {this.construct()}
                </Tabs>
            </div>
        );
    }
}


export default Demo3;


